
// Branding colours
$primary-light: #C673D7;
$primary-base: #8B50BC;
$secondary: #2D83D2;
$support: #F5CB35;
$black: #000;
$white: #fff;
$light-1: #FAFCFF;
$light-2: #F0F2F5;
$light-3: #E6E8EB;
$light-brand: #EAF3FB;
$darker-brand: #D0DBED;
$dark: #143752;


// Text colours
$text-standar: #262D33;
$text-light: #565F66;
$text-lighter: #A8AEB2;

// Universal colours
$success: #00B78B;
$danger: #F74758;

// Icon colours
$background-color: #F4F7FC;

$spacer: 1em !default;
// Font-family to use in the whole project
body,html {
    background: linear-gradient(180deg,$background-color 0%, #FFFFFF 100%);
    font-family: 'Nunito Sans', sans-serif;
    margin: 0;
    overflow-y: auto;
    overflow-x: hidden;
    height: 100%; }


// Classes to get colours easily
