.text-align-right {
    text-align: right; }
@for $i from 0 through 5 {
    @if $i == 0 {
        .m-#{$i} {
            margin:  0em !important; }
        .m-t-#{$i} {
            margin-top: 0em !important; }
        .m-r-#{$i} {
            margin-right: 0em !important; }
        .m-b-#{$i} {
            margin-bottom: 0em !important; }
        .m-l-#{$i} {
            margin-left: 0em !important; }
        .p-#{$i} {
            padding:  0em !important; }
        .p-t-#{$i} {
