ul.inline {
    li {
        display: inline-block;
        margin-right: 1em; } }


.floating-right {
    position: absolute;
    top: 0;
    right: 0; }
i {
    &.floating-right {
        @extend .primary-light;
        font-size: 30px;
 } }        // @extend .floating.right
